import React, { useMemo, useState } from "react";
import { Helmet } from "react-helmet";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import CollectionsWithTab from "../components/collections/CollectionsWithTab";
import MenuSlideBannerSection from "../components/MenuSlideBannerSection";
import CollectionsThree from "../components/collections/CollectionsThree";
import MiniSlider from "../components/banners/MiniSlider";
import { getImageUrl } from "../../../utils/oxMethods";
import BannersThree from "../components/banners/BannersThree";
// import ProductListSix from "../../../components/features/product/details/ProductListSix";

function HomePageNine() {
	const marketData = useSelector((state) => state.marketData);
	const { blocks } = useSelector((state) => state.blocks);

	const [showText, setShowText] = useState(false);
	const miniBlock = blocks?.find((block) => block.title === "mini_block");
	const { t } = useTranslation();
	const description = marketData?.settings?.seoHtml;

	// const styles = marketData?.settings?.styles;
	const latestCollection = blocks?.find(
		(block) =>
			block.title ===
			// "populary" ||
			"latest"
	);
	const discountCollection = blocks?.find(
		(block) => block.title === "discount"
	);

	const head = useMemo(() => {
		return {
			title: marketData.name,
			description: marketData.settings && marketData.settings.description,
			keyword: marketData.settings && marketData.settings.keyword,
			favicon: getImageUrl(marketData.settings.faviconImage, "50x_"),
		};
	}, []);

	if (!blocks) {
		return "";
	}

	return (
		<>
			<Helmet>
				<title>{head.title}</title>
				<meta name="description" content={head.description} />
				<meta name="keywords" content={head.keyword} />
				<meta name="apple-mobile-web-app-title" content={head.title} />
				<meta name="application-name" content={head.title} />
				<link
					rel="icon"
					type="image/png"
					href={head.favicon}
					sizes="16x16"
				/>
			</Helmet>

			<h1 className="d-none">{head.title}</h1>

			<div className="home-page-nine">
				<div className="container menu-with-slider">
					<MenuSlideBannerSection />
					<div className="menu-slider row d-none home">
						<div className="banners-childmenus">
							<BannersThree position={`top`} />
						</div>
					</div>
				</div>

				{miniBlock ? (
					<div className="container mini-slider">
						<MiniSlider
							block={miniBlock}
							options={{
								pagination: false,
								perPage: 5,
								focus: "left",
								arrows: false,
								gap: "10px",
								breakpoints: {
									991: {
										perPage: 4,
									},
									768: {
										perPage: 3,
									},
									560: {
										perPage: 2.5,
									},
								},
							}}
						/>
					</div>
				) : null}

				{blocks ? (
					<div className="collection-tabs container">
						<CollectionsWithTab
							blocks={blocks}
							collectionRows={2}
						/>
					</div>
				) : null}
				<div className="container banners-container-second">
					<BannersThree position={`bottom`} />
				</div>
				{discountCollection?.products.length > 0 ? (
					<div className="populary-collection container">
						{/* others */}
						<CollectionsThree
							collection={discountCollection}
							title={discountCollection?.name}
							width={2}
						/>
						{/* others */}

						{/* <ProductListSix menu={discountCollection} /> */}
						{/* gshop */}
					</div>
				) : null}
				{latestCollection?.products.length > 0 ? (
					<div className="populary-collection container">
						<CollectionsThree
							collection={latestCollection}
							title={"new-items"}
							width={2}
						/>
					</div>
				) : null}
				{description > 1 ? (
					<div className="container text-container">
						<div className="mb-4">
							<div
								className={`before-footer-text ${
									showText ? "active" : ""
								}`}
								dangerouslySetInnerHTML={{
									__html: description,
								}}
							/>
							<Link
								to="#"
								className={`opener ${
									showText ? "d-none" : "d-inline-block"
								}`}
								onClick={() => setShowText(!showText)}
							>
								{t("read-more")}
							</Link>
						</div>
					</div>
				) : null}
			</div>
		</>
	);
}

export default HomePageNine;
